body{
  background-image: url("./table.jpg");
  background-size: cover;
}

#calculatorWrapper{
  background-color: #080a2e;
  width: 28vw;
  margin: 0 auto;
  border-radius: 15px;
  height: 90vh;
  padding: 1%;
  box-shadow: 4px 4px 1px #bbdddd, 9px 9px 1px #080a2e;
}

#display{
  text-align: right;
  padding: 1%;
  margin: 1% auto;
  height: 20%;
  color: #bbdddd;
  overflow: hidden;
  font-size: 5rem;
}

#buttonsWrapper{
  display: flex;
  flex-wrap: wrap;
  height: 78%;
  width: 99%;
  margin: auto;
  vertical-align: middle;
}

.btn{
  width: calc(23%);
  height: calc(87% / 5);
  font-size: 50px;
  margin: 1%;
  background-color: #bbdddd;
 

}

.btn:focus{
  outline: inset #fcfcbb
}

#clear{
  width: 100%;
}